import React from 'react';
import './Bandtext.css';


function Bandtext() {
  return (   
            <>
            <div className='Banddiv' id='Home'> </div>
                <h1 className='Text1'>Party- und Hochzeitsband - stilvolle Livemusik fr deinen Anlass</h1>
                <p className='Text2'>
                    Du suchst fr die Umrahmung deines Events noch die passende Musik - wir haben sie. 
                    berzeuge dich von unserer groovigen Liveband, welche auch deinem Anlass die musikalische Note gibt. 
                    Sei es eine Hochzeit, Trauung, Party oder ein Firmenevent, wir verfgen ber ein vielseitiges Repertoire. 
                    Von Klassikern, bis hin zu aktuellen Hits, Apromusik oder einem Hochzeitswalzer garantieren wir unvergessliche 
                    Momente auf der Tanzflche. 
                </p>
            </>
       
    )
}

export default Bandtext;