import React, {useState} from 'react';
import './Band.css';
import Basil from './Solo_Basil.jpg'
import Leo from './Solo_Leo.jpg'
import Manu from './Solo_Manu.jpg'
import Stefan from './Solo_Stefan.jpg'





function Band() {

  const [show1,setShow1] = useState(false);
  
  function Enter1 (e) {
    setShow1(true);
    console.log(show1);
   }

   function Leave1 (e) {
    setShow1(false);
    console.log(show1);
   }


  const [show2,setShow2] = useState(false);
  
  function Enter2 (e) {
    setShow2(true);
    console.log(show2);
   }

   function Leave2 (e) {
    setShow2(false);
    console.log(show2);
   }
   
   
  const [show3,setShow3] = useState(false);
  
  function Enter3 (e) {
    setShow3(true);
    console.log(show3);
   }

   function Leave3 (e) {
    setShow3(false);
    console.log(show3);
   }


  const [show4,setShow4] = useState(false);
  
  function Enter4 (e) {
    setShow4(true);
    console.log(show4);
   }

   function Leave4 (e) {
    setShow4(false);
    console.log(show4);
   }

   
  return (  
    <div id='band'>
      <div className='Absatz' > 
      <h5 className='Biotitel'>Bio</h5>
      <p className='Textband'> Birdland steht fr professionelle Livemusik, welche in Kombination mit viel Leidenschaft fr  
    unvergessliche Momente sorgt. Auf Anfrage ist es auch mglich, eine kleinere Formation zu buchen,
    welche optimal zu deinem Anlass passt.</p> </div>
    <div className='Bios' >
       <div className='Quad' onMouseEnter={Enter1} onMouseLeave={Leave1}>
              {show1?<p className='Biob'> <h5 className='Biot'>Basil Zinsli</h5> 
              grndete 2011  die Acapella Formation "INVIVAS", welche sich von einer Bndner Schlerband 
                zu einer festen Grsse in der Schweizer Musikszene etabliert hat. 
                Im Herbst 2013 begann er sein Bachelorstudium mit Hauptfach Posaune 
                Klassik bei David Bruchez and der Zrcher Hochschule der Knste. Dieses 
                schloss er 2016 mit Bestnote ab. Parallel dazu besuchte er Sologesang und 
                bildete sich autodidaktisch in den Bereichen "Producing" und "Arranging" weiter. 
                Diese Vielseitigkeit bewog ihn dazu, fr das Masterstudium in die Jazz- Popabteilung zu 
                wechseln, welches er im Fach Popgesang 2019 abschloss. </p>
              :<div className='switch'>
                    <div  className='Crop'> 
                      <img className='Portraits' src={Basil} alt="Basil"></img>
                    </div>
                  <h1 className='Names'>Basil Zinsli</h1>
                  <h3 className='Func'>Lead Singer</h3>
                </div>}
        </div>

        <div className='Quad' onMouseEnter={Enter2} onMouseLeave={Leave2}>
              {show2?<p className='Biob'> <h5 className='Biot'>Leonardo Simovic</h5> ist 30 Jahre alt und wohnt in Zrich. 
        Er arbeitet als Fullstack Developer bei Auterion. Zu seinen Lieblingsmusikern gehren Bootsy Collins und Victor Wooten. 
        Entsprechend seinen Vorbildern legt er bei Birdland einen soliden Boden fr Groove und Harmonie. </p>
              :<div className='switch'>
                    <div  className='Crop'> 
                      <img className='Portraits' src={Leo} alt="Basil"></img>
                    </div>
                  <h1 className='Names'>Leonardo Simovic</h1>
                  <h3 className='Func' >Bass</h3>
                </div>}
        </div>

        <div className='Quad' onMouseEnter={Enter3} onMouseLeave={Leave3}>
              {show3?<p className='Biob'> <h5 className='Biot'>Manuel Zolliker</h5>ist als Pianist in der Schweizer Gospel-, 
              Pop- und Jazzszene aktiv. Seit seinem Studium an der Zrcher Hochschule 
              der Knste in Jazz-Klavier und Schulmusik II arbeitet er als Klavierlehrer an der 
              Pdagogischen Hochschule in Zrich und als Gymnasiallehrer im Fach Musik. 
              Daneben ist er auch als Chorleiter ttig. </p>
              :<div className='switch'>
                    <div  className='Crop'> 
                      <img className='Portraits' src={Manu} alt="pic"></img>
                    </div>
                  <h1 className='Names'>Manuel Zolliker</h1>
                  <h3 className='Func'>Piano</h3>
                </div>}
        </div>

        <div className='Quad' onMouseEnter={Enter4} onMouseLeave={Leave4}>
              {show4?<p className='Biob'> <h5 className='Biot'>Stefan Lengg</h5> ist seit dem Abschluss des Musikstudiums an der Swiss Music Academy 
              als Schlagzeuger / 
              Perkussionist im Bereich Pop und Gospel aktiv.
              Nebst dem Privatunterricht arbeitet er in der Herstellung von Kontaktlinsen. </p>
              :<div className='switch'>
                    <div  className='Crop'> 
                      <img className='Portraits' src={Stefan} alt="pic"></img>
                    </div>
                  <h1 className='Names'>Stefan Lengg</h1>
                  <h3 className='Func'>Drums / Booking</h3>
                </div>}
        </div>
    </div>
  </div>  
  )
}

export default Band;
